#root {
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem;
  text-align: center;
}
:root {
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;

  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.87);
  background-color: #242424;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.completion {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.map-hint {
  width: 75%;
  object-fit: cover;
}

a {
  font-weight: 500;
  color: #001930;
  text-decoration: inherit;
}
a:hover {
  opacity: 0.5;
}

body {
  min-height: 100vh;
  margin: 0;
  display: flex;
  place-items: center;
  min-width: 320px;
}

h1 {
  font-size: 3.2em;
  line-height: 1.1;
}

.logo {
  object-fit: cover;
  max-width: 300px;
}

.audio-controls {
  button {
    width: 220px;
    color: white;
    background-color: #001930;
  }
}

button {
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  background-color: #c2b8b8;
  cursor: pointer;
  transition: border-color 0.25s;
}
button:hover {
  border-color: #000000;
}
button:focus,
button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}

@media (prefers-color-scheme: light) {
  :root {
    color: #001930;
    background-color: #0c192c;
  }
  a:hover {
    opacity: 0.5;
  }
  button {
    background-color: #6da03c;
  }
}

footer {
  display: flex;
  flex-direction: column;
  bottom: 0;
  opacity: 0.5;
  font-size: smaller;
}

.container {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.bubbles {
  position: relative;
  display: flex;
}

.bubbles span {
  position: relative;
  width: 30px;
  height: 30px;
  background: #4fc3dc;
  margin: 0 4px;
  border-radius: 50%;
  box-shadow: 0 0 0 10px #44fc3d44, 0 0 50px #4fc3dc, 0 0 100px #4fc3dc;
  animation: animate 15s linear infinite;
  animation-duration: calc(125s / var(--i));
}
.bubbles span:nth-child(even) {
  background: #4f5bdc;
  box-shadow: 0 0 0 10px #3d459e, 0 0 50px #4f5bdc, 0 0 100px #4f5bdc;
}
@keyframes animate {
  0% {
    transform: translateY(100vh) scale(0);
  }
  100% {
    transform: translateY(-10vh) scale(1);
  }
}
